.primary-menu {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  padding-top: 80px;
  padding-bottom: 80px;
  background: rgba(255,255,255, .95);
  z-index: 1000;
  transform: translate3d(0, -100%, 0);
  transition: transform .35s;
  
  &.is-active {
    transform: translate3d(0, 0, 0);
  }
}