.dropdown,
.dropup {
  position: relative
}

// Dropdown Menu
.dropdown-menu {
  display: none;
  left: 0;
  min-width: 10rem;
  padding: 0.5rem 0;
  position: absolute;
  top: 100%;
  z-index: 20;
  font-size: 1rem;
  color: #212529;
  text-align: left;
  list-style: none;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
}

.dropdown-toggle {
  display: inline-block;
}

// Flecha de direccion del dropdown
.dropdown-toggle::after {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 0.255em;
  vertical-align: 0.255em;
  content: "";
  border-top: 0.3em solid;
  border-right: 0.3em solid transparent;
  border-bottom: 0;
  border-left: 0.3em solid transparent;
}

.dropdown-toggle:empty::after {
  margin-left: 0;
}

.dropup .dropdown-toggle::after {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 0.255em;
  vertical-align: 0.255em;
  content: "";
  border-top: 0;
  border-right: 0.3em solid transparent;
  border-bottom: 0.3em solid;
  border-left: 0.3em solid transparent;
}

.dropup .dropdown-toggle:empty::after {
  margin-left: 0;
}

.dropdown.is-hoverable:hover .dropdown-menu,
.dropdown.show .dropdown-menu,
.dropup.is-hoverable:hover .dropdown-menu,
.dropup.show .dropdown-menu {
  display: block;
}

// Posicion del dropdown
.dropdown.dropdown-right .dropdown-menu,
.dropup.dropup-right .dropdown-menu {
  left: auto;
  right: 0;
}

.dropup .dropdown-menu {
  bottom: 100%;
  padding-bottom: 4px;
  padding-top: unset;
  top: auto;
}

// Dropdown Items or anchor tags
.dropdown-item,
.dropdown-menu a {
  display: block;
  width: 100%;
  padding: 0.25rem 1.5rem;
  color: #212529;
  text-align: inherit;
  white-space: nowrap;
  background: none;
}

.dropdown-item:focus, .dropdown-item:hover, .dropdown-menu a:hover {
  color: #16181b;
  background-color: whitesmoke;
}

.dropdown-item.active, .dropdown-item:active, .dropdown-menu a:active {
  color: #fff;
  background-color: #007bff;
}

.dropdown-item.disabled, .dropdown-item:disabled, .dropdown-menu a:disabled {
  color: #868e96;
  background-color: transparent;
}

.dropdown-divider {
  height: 0;
  margin: 0.5rem 0;
  overflow: hidden;
  border-top: 1px solid #e9ecef;
}
