@responsive {
  .list-reset {
    margin: 0;
  }
}

.menu-toggle {
  position: fixed;
  right: 30px;
}

.burger-toggle {
  padding: 10px;
  padding-right: 0;
  transform: translate3d(0, 0, 0);
  cursor: pointer;
  z-index: 1001;
}

.burger-toggle ._layer {
  background: #222b2f;
  margin-bottom: 4px;
  border-radius: 2px;
  width: 30px;
  height: 4px;
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: all .2s;
}

.burger-toggle ._layer:last-child {
  margin-bottom: 0;
}

.burger-toggle:hover .-top {
  transform: translateY(-100%);
}

.burger-toggle:hover .-bottom {
  transform: translateY(100%);
}

.burger-toggle.is-open .-top {
  transform: translateY(200%) rotate(45deg);
}

.burger-toggle.is-open .-mid {
  opacity: 0;
}

.burger-toggle.is-open .-bottom {
  transform: translateY(-200%) rotate(-45deg);
}

.burger-toggle.is-open ._layer {
  background: #222b2f;
}

.menu-toggle--white ._layer {
  background: white;
}

.menu-toggle--black ._layer {
  background: #222b2f;
}

.primary-menu {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  padding-top: 80px;
  padding-bottom: 80px;
  background: rgba(255, 255, 255, 0.95);
  z-index: 1000;
  transform: translate3d(0, -100%, 0);
  transition: transform .35s;
}

.primary-menu.is-active {
  transform: translate3d(0, 0, 0);
}

body {
  font-family: 'Quicksand', sans-serif;
}

.global-wrapper {
  background-image: linear-gradient(-135deg, rgba(198, 78, 255, 0.5) 0%, rgba(139, 21, 255, 0.5) 100%), url("../images/mountains.jpg");
}

.portfolio-item .thumbnail {
  width: 100%;
  height: 360px;
  background-size: cover;
  background-position: center;
}
