@mixin burger-toggle() {

  padding: 10px;
  padding-right: 0; // No extra padding on right (only 15px from container)
  transform: translate3d(0, 0, 0);
  cursor: pointer;
  z-index: 1001;

  // Bars styling
  ._layer {
    background: $black;
    margin-bottom: 4px;
    border-radius: 2px;
    width: 30px;
    height: 4px;
    opacity: 1;
    transform: translate3d(0, 0, 0);
    transition: all .2s;
    &:last-child  {
      margin-bottom: 0;
    }
  }

  // Hover state
  // Expand
  &:hover .-top {
    transform: translateY(-100%);
  }
  &:hover .-bottom {
    transform: translateY(100%);
  }

  // Active state
  &.is-open .-top {
    transform: translateY(200%) rotate(45deg);
  }
  &.is-open .-mid {
    opacity: 0;    
  }
  &.is-open .-bottom {
    transform: translateY(-200%) rotate(-45deg);
  }

}