@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon/icomoon.eot?k835be');
  src:  url('../fonts/icomoon/icomoon.eot?k835be#iefix') format('embedded-opentype'),
    url('../fonts/icomoon/icomoon.ttf?k835be') format('truetype'),
    url('../fonts/icomoon/icomoon.woff?k835be') format('woff'),
    url('../fonts/icomoon/icomoon.svg?k835be#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-camera:before {
  content: "\e90f";
}
